import React, { useState } from 'react';
import Part from '../components/Part';

// `Material` Components.
import Grid from '@material-ui/core/Grid';

// Stylesheet.
import useStyles from './Home.style';

const Home = () => {
  const classes = useStyles();

  const [partItemsList, setPartItem] = useState([
    { name: 'first part', id: 124, status: 'Checked In' },
    { name: 'second part', id: 244, status: 'Checked Out' },
    { name: 'third part', id: 244, status: 'Checked In' },
    { name: 'fourth part', id: 244, status: 'Checked Out' }
  ]);

  return (
    <Grid container>
      {partItemsList.map(partItem => {
        return (
          <Grid item xl={3} className={classes.item}>
            <Part name={partItem.name} id={partItem.id} status={partItem.status} />
          </Grid>
        );
      })}
    </Grid>
  );
};

export default Home;
